import React from 'react';
import LOGOIMAGE from '../image/logo.png';
import IMAGE from '../image/Capture.PNG';
import IMAGE2 from '../image/Capture2.PNG';

function Header() {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">

        <div className="collapse navbar-collapse" id="navbarTogglerDemo01">

          <img src={LOGOIMAGE} alt="" width="50px" height="30px" />
          <form className="d-flex">
            <input className="form-control me-2 " type="search" placeholder="Pakistan" aria-label="Search" />
          </form>

          <form >
            <input className="form" id="search2" type="search" placeholder="Find Car Mobile Phone and more ..." aria-label="Search" />
          </form>

          <img className="img2" src={IMAGE} alt="" />
          <img src={IMAGE2} alt="" />
        </div>
      </div>
    </nav>
  )
}

export default Header;
