import React from 'react';
import footermobile from '../image/footermobile.PNG';
import footermobile2 from '../image/appstore.PNG';

function Content() {
    return (
        <div>
            <div className="containerfooter">
                <div className="imagefooter">
                    <img src={footermobile} alt="" />
                </div>
                <div>
                    <h2 className="div2">TRY THE OLX APP</h2>
                    <p><b>Buy, sell and find just about anything using <br></br>the app in your mobile</b></p>
                </div>
                <div className="imagefooter2">
                    <img src={footermobile2} alt="" />
                </div>
            </div>
        </div>
    )
}

export default Content;